import { Component, OnInit, Injector, ViewChild, TemplateRef, HostListener } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { VendorService } from 'src/app/api/controllers/Vendor';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ViewVendorComponent } from '../view-vendor/view-vendor.component';

@Component({
  selector: 'app-all-vendors',
  templateUrl: './all-vendors.component.html',
  styleUrls: ['./all-vendors.component.scss']
})
export class AllVendorsComponent implements OnInit {

  isVendorCategoryLoaded: boolean = false;
  isVendorTypeLoaded: boolean = false;
  isVendorTableLoaded: boolean = false;

  vendorCategoryId: any;
  vendorTypeId: any;

  vendorCategoryDataList: any;
  vendorTypeDataList:any;

  vendorTableList: Array<any> = [];
  

  vendorData: string = "";
  unitFieldType: string = "unitno";
  unitOrder: boolean = true;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;
  totalFilterItems: any;

  modalService: any;

  vendor: any;
  dialogRef: any;

  vendorFilterList: any;

  categoryName: string = "";

  selectedInput: string = "";
  columnField: any = {};

  public columnData: any;

  gridSourceData: any;

  @ViewChild("viewVendorRef", { static: false }) viewVendorRef: TemplateRef<any>;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;


  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private injector: Injector,
    public dialog: MatDialog,
    private vendorService: VendorService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private sessionService: SessionService
  ) {
    this.modalService = this.injector.get(ModalService);
  }

  vendorPrint(vendor) {
    this.dialog.open(this.viewVendorRef, {
      panelClass: 'material',
      disableClose: true,
      data: vendor
    });

  }

  getPrintParams(event) {
    this.datagrid.exportdata(event, 'vendorData');
  }

  onGlSearchFilter() {

    if (this.vendorData != "") {

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.vendorData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if (item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();

    }
    else {
      this.datagrid.clearfilters();
    }

  }


  getVendorCategory(event) {
    this.vendorCategoryId = event[0].lookupValueId
    this.getVendorCategoryData();
  }

  getVendorType(event){
    this.vendorTypeId = event[0].lookupValueId
    this.getVendorCategoryData();
  }

  getVendorCategoryName(id){

    var data = _.filter(this.vendorCategoryDataList, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getVendorTypeName(id){

    var data = _.filter(this.vendorTypeDataList, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getVendorCategoryData(){

    this.isVendorTableLoaded = false;

    let params = {
      apartmentId: parseInt(this.sessionService.apartmentId)
    }

    this.vendorService.getVendorByApartmentId(params).subscribe((res: any) => {

      var vendorListData;
      
      if(this.vendorCategoryId == undefined && this.vendorTypeId == undefined){
        vendorListData = res.filter(item => {
          return item.isActive
        });
      }
      
      if(this.vendorCategoryId != undefined) {
        vendorListData = res.filter(item => {
          return (item.isActive && item.vendorCategoryId == this.vendorCategoryId)
        });
      }
      
      if(this.vendorTypeId != undefined) {
        vendorListData = res.filter(item => {
          return (item.isActive && item.vendorTypeId == this.vendorTypeId)
        });
      }

      if(this.vendorCategoryId != undefined && this.vendorTypeId != undefined) {
        vendorListData = res.filter(item => {
          return (item.isActive && (item.vendorCategoryId == this.vendorCategoryId) && (item.vendorTypeId == this.vendorTypeId) )
        });
      }

      vendorListData.map(item => {
        let catArr = this.vendorCategoryDataList.filter( data => data.lookupValueId === item.vendorCategoryId);
        item.vendorCategoryName = (() => { return catArr.length != 0 ? catArr[0].lookupValueName : ''  })();
        
        let typeArr = this.vendorTypeDataList.filter( data => data.lookupValueId === item.vendorTypeId);
        item.vendorTypeName = (() => { return typeArr.length != 0 ? typeArr[0].lookupValueName : ''  })();

      });

      let gridSourceData = {
        localdata: vendorListData,
        datatype: "array"
      }

      this.vendorTableList = new jqx.dataAdapter(gridSourceData);
      this.isVendorTableLoaded = true;
      this.totalItems = this.vendorTableList.length;


    },
    error => {

    });

  }

  showDeleteConfirmModal(id) {
    this.modalService.showConfirmModal(id);
  }

  showVendorInfo(vendor: any) {

    this.dialog.open(this.viewVendorRef, {
      panelClass: 'material-dialog-medium',
      disableClose: true,
      data: vendor
    });

  }

  isNoItemsAvailable() {
    return this.totalItems == 0 ? true : false;
  }

  dialogClose(){
    this.dialogRef.close();
  }

  @HostListener('window:onEditVendor', ['$event.detail'])
  onEditVendor(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let vendorId = dataRecord.vendorId;
    this.router.navigateByUrl('/ams/vendor/edit-vendor/' + vendorId);
  }


  @HostListener('window:onDeleteVendor', ['$event.detail'])
  onDeleteVendor(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let vendorId = dataRecord.vendorId
    console.log(333);
    this.modalService.showConfirmModal(vendorId);
  }

  @HostListener('window:onViewVendor', ['$event.detail'])
  onViewVendor(detail) {
    
    let dataRecord = this.datagrid.getrowdata(detail.rowId)

    const dialogRef = this.dialog.open(this.viewVendorRef, {
      panelClass: 'material-dialog-medium',
      data: dataRecord
    });

  }

  

  ngOnInit() {

   /* this.vendorTypeData = [{
      id:225,
      label: 'Goods'
    }, {
      id:226,
      label: 'Services'
    }] */


    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
      text: 'Vendor ID',
      datafield: 'vendorId',
      minwidth: 130,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Vendor Name',
      datafield: 'vendorName',
      minwidth: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Contact Person',
      datafield: 'contactPerson',
      minwidth: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Vendor Category',
      datafield: 'vendorCategoryName',
      minwidth: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Vendor Type',
      datafield: 'vendorTypeName',
      minwidth: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Vendor Status',
      datafield: 'vendorStatus',
      minwidth: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Phone No',
      datafield: 'phone1',
      minwidth: 170,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'VAT',
      datafield: 'vatorTin',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Percentage Tax',
      datafield: 'tax1',
      minwidth: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'With Holding Tax',
      datafield: 'tax2',
      minwidth: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      minwidth: 150,
      cellclassname: 'action-cell',
      cellsrenderer: (row : number, column, value): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="viewVendor(' + row + ')"><i class="fa fa-eye text-green-500" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="editVendorEvent(' + row + ')"><i class="fa fa-pencil text-blue-500" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="deleteVendorEvent(' + row + ')"><i class="fa fa-trash text-red-500" aria-hidden="true"></i></a></div>'

      },
      renderer: columnrenderer
    }];


    let params = {
      LookupTypeId: 57
    }

    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {

      this.isVendorCategoryLoaded = true;

      this.vendorCategoryDataList = res.filter(item => {
        return item.isActive;
      });

      //vendor type
      let vendorTypeParams = {
        LookupTypeId: 58
      }
      this.lookupService.getLookupValueByLookupTypeId(vendorTypeParams).subscribe((res: any) => {

        this.isVendorTypeLoaded = true;
        
        this.vendorTypeDataList = res.filter(item => {
          return item.isActive;
        });

        this.getVendorCategoryData();

      });

    });


    // delete vendor
    this.sharedService.unitlistdeleteindexcast.subscribe(id => {

      if (id != null) {

        var params = {
          vendorId: id,
          deleteBy: parseInt(this.sessionService.userId)
        }


        this.vendorService.deleteVendor(params).subscribe((res: any) => {

          _.each(this.vendorTableList, (type) => {
            if (type.vendorId == id) {
              type.active = false;
            }
          })

          setTimeout(() => {

            this.vendorTableList = this.vendorTableList.filter((type) => type.vendorId !== id);
            this.totalItems = this.vendorTableList.length;
            this.sharedService.openSnackBar('Vendor Deleted', 'success');
            this.sharedService.setUnitListDeleteIndex(null);

          }, 500)


        }, error => {
          console.log(error);
        });


      }

    });


  }


}

let viewVendor = row => {
  var event = new CustomEvent('onViewVendor', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewVendor = viewVendor;

let deleteVendorEvent = row => {
  console.log(row)
  var event = new CustomEvent('onDeleteVendor', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).deleteVendorEvent = deleteVendorEvent;

let editVendorEvent = row => {
  var event = new CustomEvent('onEditVendor', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editVendorEvent = editVendorEvent;

